$base: 1.6rem;

.arrowup {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    transform: skew(0deg, -13deg);
}

.chevron {
    position: absolute;
    width: $base * 3.5;
    height: $base * 0.8;
    opacity: 0;
    transform: scale(0.3);
    animation: move-chevron 3s ease-out infinite;
}

.chevron:first-child {
    animation: move-chevron 3s ease-out 1s infinite;
}

.chevron:nth-child(2) {
    animation: move-chevron 3s ease-out 2s infinite;
}

.chevron:before,
.chevron:after {
    content: "";
    position: absolute;
    top: 0;
    height: 100%;
    width: 50%;
    background: #965de9;
}

.chevron:before {
    left: 0;
    transform: skewY(330deg);
}

.chevron:after {
    right: 0;
    width: 50%;
    transform: skewY(30deg);
}

@keyframes move-chevron {
    25% {
        opacity: 0;
        transform: translateY($base * 8) scale(0.5);
    }
    33.3% {
        opacity: 1;
        transform: translateY($base * 5.2);
    }
    66.6% {
        opacity: 1;
        transform: translateY($base * 3.8);
    }
    100% {
        opacity: 0;
    }
}
