}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

address {
  font-style: normal;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
  --bgColor: linear-gradient(83.2deg, #965de9 10.8%, #6358ee 94.3%);
  --bgColor-second: linear-gradient(83.2deg, #965de94d 10.8%, #6358ee1a 94.3%);
  --buttons-color: #fff;
  --border-color: #fff;
  --font-color: #fff;
  --placeholder-color: #fff;
  --selection-color: #fff;
  --selection-bg-color: #00000040;
  --rows-size: 320px;
  --icons-hover: #3f57b3;
  --icons-font-size: 12px;
  --icons-font-color: #212121;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

body {
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
  font-family: Lexend Deca, sans-serif;
  line-height: 2;
}

@media screen and (max-width: 980px) {
  #NotiflixNotifyWrap {
    margin: auto !important;
    inset: 82px 10px auto !important;
  }
}

.container {
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
}

.search__container {
  background-image: var(--bgColor);
  justify-content: center;
  padding: 10px 20px;
  display: flex;
  box-shadow: 0 1px 3px #0003, 0 1px 1px #00000024, 0 2px 1px -1px #0000001f;
}

.search__box {
  width: 100%;
  max-width: 360px;
  height: 60px;
  border-radius: 120px;
  margin: 0 auto;
  position: relative;
}

.search__find-icon {
  pointer-events: none;
  will-change: transform;
  transform-origin: center;
  -o-transition: transform 400ms 220ms cubic-bezier(.19, 1, .22, 1);
  font-size: 1.22em;
  transition: transform .4s cubic-bezier(.19, 1, .22, 1) .22s;
  left: 5px;
}

.search__find-icon--rotate {
  transform: rotate(90deg);
}

.search__find-icon, .search__go-btn {
  height: 60px;
  width: 68px;
  text-align: center;
  fill: var(--buttons-color);
  position: absolute;
  top: 0;
}

.search__svg {
  height: 100%;
  width: 24px;
  margin: auto;
  display: block;
}

.search__go-btn {
  pointer-events: none;
  will-change: opacity;
  cursor: default;
  opacity: 0;
  -o-transition: opacity 190ms ease-out, transform 260ms cubic-bezier(.19, 1, .22, 1);
  background-color: #0000;
  border: none;
  outline: none;
  transition: opacity .19s ease-out, transform .26s cubic-bezier(.19, 1, .22, 1);
  right: 5px;
  transform: rotate(45deg);
}

.search__go-btn--goin {
  opacity: 1;
  pointer-events: all;
  cursor: pointer;
  transform: rotate(0);
  -webkit-transform: rotate(0);
  -moz-transform: rotate(0);
  -o-transform: rotate(0);
  -o-transition: opacity 190ms ease-out, transform 260ms 20ms cubic-bezier(.19, 1, .22, 1);
  transition: opacity .19s ease-out, transform .26s cubic-bezier(.19, 1, .22, 1) 20ms;
}

.search__border {
  width: 100%;
  max-width: 360px;
  height: 60px;
  display: block;
}

.search__border-path {
  fill: none;
  stroke: var(--border-color);
  stroke-width: 5px;
  stroke-miterlimit: 10;
  stroke-dasharray: 740;
  stroke-dashoffset: 0;
  -o-transition: stroke-dashoffset 400ms cubic-bezier(.6, .04, .735, .99);
  transition: stroke-dashoffset .4s cubic-bezier(.6, .04, .735, .99);
}

.search__box--searching .search__border-path {
  stroke-dasharray: 740;
  stroke-dashoffset: 459px;
  -o-transition: stroke-dashoffset 650ms cubic-bezier(.755, .15, .205, 1);
  transition: stroke-dashoffset .65s cubic-bezier(.755, .15, .205, 1);
}

.search__box--searching .search__input {
  box-shadow: none;
}

.search__input {
  width: 100%;
  height: 100%;
  color: var(--font-color);
  letter-spacing: -.015em;
  background: #fff0;
  border: none;
  border-radius: 120px;
  outline: none;
  padding: 0 68px;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 1.2em;
  font-weight: 400;
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (min-width: 390px) {
  .search__input {
    transition: box-shadow .65s cubic-bezier(.755, .15, .205, 1);
    box-shadow: 0 6px 8px #00000026;
  }
}

.search__input::-webkit-input-placeholder {
  color: var(--placeholder-color);
}

.search__input::-moz-placeholder {
  color: var(--placeholder-color);
}

.search__input:-ms-input-placeholder {
  color: var(--placeholder-color);
}

.search__input:-moz-placeholder {
  color: var(--placeholder-color);
}

.search__input::selection {
  color: var(--selection-color);
  background: var(--selection-bg-color);
}

.gallery {
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: var(--rows-size);
  grid-gap: 12px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.gallery__link {
  height: calc(100% - 70px);
  width: 100%;
  opacity: 0;
  text-decoration: none;
  display: flex;
}

.gallery__card {
  width: 100%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  position: relative;
  box-shadow: 0 1px 3px #0003, 0 1px 1px #00000024, 0 2px 1px -1px #0000001f;
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.gallery__image:hover {
  cursor: zoom-in;
  transform: scale(1.03);
}

.gallery__info {
  padding: 10px 20px;
  display: flex;
}

.gallery__item {
  width: 25%;
  -webkit-user-select: none;
  user-select: none;
  flex-direction: column;
  font-size: 13px;
  display: flex;
  position: relative;
}

.gallery__item:nth-child(1) {
  width: 22%;
}

.gallery__item:nth-child(3) {
  width: 28%;
}

.gallery__item > a {
  margin-right: auto;
}

.gallery__icon-wrapper {
  font-size: var(--icons-font-size);
  color: var(--icons-font-color);
  cursor: pointer;
  align-items: center;
  margin-right: auto;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), fill .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.gallery__likes-icon > .liked {
  display: none;
}

.gallery__icon-wrapper:hover {
  fill: var(--icons-hover);
  color: var(--icons-hover);
}

.gallery__icon {
  height: 14px;
  width: 14px;
  margin-right: 4px;
}

.gallery--empty-page:after {
  content: "";
  height: calc(100vh - 200px);
  background-image: url("no-image.91540cb0.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 150px;
  display: block;
}

.gallery__card.new:after {
  content: "";
  width: 100%;
  height: calc(100% - 70px);
  opacity: 1;
  background-color: #cbd5e0;
  position: absolute;
}

.gallery__card:after {
  content: "";
  width: 100%;
  height: calc(100% - 70px);
  opacity: 0;
  background-color: #cbd5e0;
  transition: opacity 1s ease-out;
  position: absolute;
}

.gallery__icon-wrapper:after {
  content: "";
  width: 70%;
  height: 15px;
  opacity: 0;
  background-color: #a3bffa;
  position: absolute;
}

.gallery__item:after {
  content: "";
  width: 85%;
  height: 20px;
  opacity: 0;
  background-color: #a0aec0;
  position: absolute;
  top: 3px;
}

.new .gallery__icon-wrapper:after, .new .gallery__item:after {
  opacity: 1;
  animation: pulse 1.4s cubic-bezier(.4, 0, .6, 1) infinite;
}

.gallery__item > b, .gallery__icon-wrapper > .gallery__icon, .gallery__icon-wrapper > p {
  opacity: 1;
  transition: opacity .3s;
}

.new .gallery__item > b, .new .gallery__icon-wrapper > .gallery__icon, .new .gallery__icon-wrapper > p {
  opacity: 0;
  transition: opacity .3s;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }

  50% {
    opacity: .3;
  }
}

.loaded {
  opacity: 1;
  z-index: 10;
  transition: height 1.5s .5s, opacity 1s .8s;
}

.custom-scrollbar::-webkit-scrollbar {
  display: none;
}

