.loading {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.loading ul li {
  width: 40px;
  height: 40px;
  background: #fff;
  border-radius: 50%;
  list-style: none;
  animation: grow 1.6s ease-in-out infinite;
}

@keyframes grow {
  0%, 40%, 100% {
    transform: scale(.2);
  }

  20% {
    transform: scale(1);
  }
}

.loading ul li:nth-child(1) {
  background: #ff0;
  animation-delay: -1.4s;
  box-shadow: 0 0 50px #ff0;
}

.loading ul li:nth-child(2) {
  background: #76ff03;
  animation-delay: -1.2s;
  box-shadow: 0 0 50px #76ff03;
}

.loading ul li:nth-child(3) {
  background: #f06292;
  animation-delay: -1s;
  box-shadow: 0 0 50px #f06292;
}

.loading ul li:nth-child(4) {
  background: #4fc3f7;
  animation-delay: -.8s;
  box-shadow: 0 0 50px #4fc3f7;
}

.loading ul li:nth-child(5) {
  background: #ba68c8;
  animation-delay: -.6s;
  box-shadow: 0 0 50px #ba68c8;
}

.loading ul li:nth-child(6) {
  background: #f57c00;
  animation-delay: -.4s;
  box-shadow: 0 0 50px #f57c00;
}

.loading ul li:nth-child(7) {
  background: #673ab7;
  animation-delay: -.2s;
  box-shadow: 0 0 50px #673ab7;
}

.has-lightbox .download {
  z-index: 10060;
  cursor: pointer;
  will-change: box-shadow, transform;
  color: #fff;
  height: 48px;
  text-shadow: 0 1px #0006;
  background: radial-gradient(100% 100% at 100% 0, #89e5ff 0%, #5468ff 100%);
  border: 0;
  border-radius: 6px;
  outline: 0;
  padding: 0 32px;
  font-size: 18px;
  line-height: 2.5;
  transition: box-shadow .15s, transform .15s, opacity .3s;
  animation: fadein .3s;
  display: inline-block;
  position: fixed;
  bottom: 0;
  right: 50%;
  transform: translate(50%, -50%);
  box-shadow: 0 2px 4px #2d234266, 0 7px 13px -3px #2d23424d, inset 0 -3px #3a416f80;
}

.has-lightbox .download:hover {
  transform: translate(50%, calc(-50% - 2px));
  box-shadow: 0 4px 8px #2d234266, 0 7px 13px -3px #2d23424d, inset 0 -3px #3c4fe0;
}

.has-lightbox .download:active {
  transform: translate(50%, calc(2px - 50%));
  box-shadow: inset 0 3px 7px #3c4fe0;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.arrowup {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  transform: skew(0deg, -13deg);
}

.chevron {
  width: 5.6rem;
  height: 1.28rem;
  opacity: 0;
  animation: move-chevron 3s ease-out infinite;
  position: absolute;
  transform: scale(.3);
}

.chevron:first-child {
  animation: move-chevron 3s ease-out 1s infinite;
}

.chevron:nth-child(2) {
  animation: move-chevron 3s ease-out 2s infinite;
}

.chevron:before, .chevron:after {
  content: "";
  height: 100%;
  width: 50%;
  background: #965de9;
  position: absolute;
  top: 0;
}

.chevron:before {
  left: 0;
  transform: skewY(330deg);
}

.chevron:after {
  width: 50%;
  right: 0;
  transform: skewY(30deg);
}

@keyframes move-chevron {
  25% {
    opacity: 0;
    transform: translateY(12.8rem)scale(.5);
  }

  33.3% {
    opacity: 1;
    transform: translateY(8.32rem);
  }

  66.6% {
    opacity: 1;
    transform: translateY(6.08rem);
  }

  100% {
    opacity: 0;
  }
}

